import React from "react";
import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";
import styles from "./JobItem.module.css"
import { useParams } from "react-router-dom";

export default function JobDetail() {

    let {id} = useParams();
    let [item, setItem]=React.useState({});
    let isMounted = React.useRef(true)

    React.useEffect(()=>{
        function readItem() {
            import("../../model/db").then(async(db)=>{
                setItem(await db.readById(id,"jobs"))
            })
        }

        if (isMounted.current) {
            readItem();
        }

        return ()=>{
            isMounted.current=false;
        }

    })




    return (<Container>
        <Card className={styles.cardwidth}>
            <Card.Body>
                <Row>
                    <Col md={12}>
                       <Card.Img variant="top" src="https://via.placeholder.com/25x25" />
                    </Col>
                    <Col className="d-flex justify-content-between" md={12}>
                        <div>
                            <Card.Title className={styles.error}>{item.title ? item.title : "Software Developer"}</Card.Title>
                            <Card.Subtitle className="mb-2 text-muted">{item.jobLocation ? item.jobLocation : "USA"}</Card.Subtitle>
                            <Card.Text>
                                {item.jobType ?? "Remote"}
                            </Card.Text>
                            <Card.Text>
                                {item.description ?? "I am the description...."}
                            </Card.Text>
                        </div>
                        <Card.Link href="#" onClick={() => alert("Bookmark me")}><span className="bi bi-bookmark-plus"></span> </Card.Link>
                    </Col>
                </Row>
            </Card.Body>
        </Card>

    </Container>)
}